body,
h1,
h2,
h3,
h4,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  height: 100%;
}

body {
  background-color: black;
  font-family:$bodyFont;
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 85%;
  max-width: 1280px;
  margin: 0 auto;
}

h1 {
  font-family: $headingFont;
  text-shadow: 3px 3px 3px $accentColour;
  margin: 2rem 0 1rem 0;
  font-size: 6rem;

  a{
    padding:0;
  }
}

h2 {
  font-family: $headingFont;
  text-shadow: 2px 2px 2px $accentColour;
  margin: 0.5rem 0 3rem 0;
  font-size: 4rem;
}


h3 {
  font-family: $headingFont;
  text-shadow: 2px 2px 2px $accentColour;
  margin: 0.5rem 0 1rem 0;
  margin: 0.5rem 0 1rem 0;
  font-size: 3rem;
}

button:hover,
button:focus {
  background-color: $accentColour;
  border: 1px solid $accentColour;
}

button:active {
  top: 1px;
}

li {
  a {
    background-color: $darkestColour;
    color: $lightestColour;
    border-radius: 0.25em;
    padding: 1rem 2rem 1rem 2rem;
    box-shadow: 2px 2px 2px rgba(39, 35, 35, 0.664);
    position: relative;
    cursor: pointer;
    transition: all 0.2s;
    border: 1px solid $darkestColour;
  }

  a:hover,
  a:focus {
    background-color: $accentColour;
    border: 1px solid $accentColour;
  }

  a:active {
    top: 1px;
  }
}
a {
  text-decoration: none;
  color: white;
  padding:3rem 4rem;
}
